body {
	header {
		box-sizing:border-box;
		margin:0 auto;
		padding:8px;
		background:linear-gradient(to left top, blue, red);
		color:#ffffff;

		h1 {
			font-size:30px;
		}

		h2 {
			font-size:30px;
		}

		p {
			font-weight:700;
			font-size:20px;
		}

		@media (min-width:300px) {
			font-weight:200;
		}

		@media (min-width:600px) {
			font-weight:200;
		}

		@media (max-width:500px) {
			font-size:30px;
		}

		@media (max-width:700px) {
			font-size:40px;
		}
	}

	footer {
		font-size:20px;

		@media (max-width:700px) {
			font-size:30px;
		}

		@media (max-width:500px) {
			font-size:40px;
		}
	}
}
